import Axios from 'axios';

import JWTUtil from './JWTUtil'  ;
var CallApi={};


CallApi.getJWT=(ip)=>{
    
}

CallApi.callEndPoint=(url,ip,flag)=>{
        let promise=new Promise((resolve,reject)=>{
            console.log(flag)
            if(flag!==undefined){
            let p=JWTUtil.getJWT(ip)
                 p.then((result)=>{
                  console.log(result)
                  Axios.post(url,{jwt:result} ).then(res => {
                    // res.data;
                     resolve(res.data)
                 }).catch(err=>{
                     reject(err);
                 });
                 });
                }else{

                    Axios.post(url,ip ).then(res => {
                        // res.data;
                         resolve(res.data)
                     }).catch(err=>{
                         reject(err);
                     });

                }
           
           
           
          });
      return promise;
      


    

}


export default CallApi;