import React, { useState, useEffect, useRef } from 'react'
import { FaEye, FaEyeSlash } from 'react-icons/fa';
import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CContainer,
  CForm,
  CFormInput,
  CInputGroup,
  CInputGroupText,
  CRow,
  CHeader,
  CDropdownMenu,
  CDropdownToggle,
  CDropdownItem,
  CNavLink,
  CHeaderNav,
  CNavItem,
  CHeaderToggler,
  CHeaderBrand,
  CDropdownDivider,
  CCollapse,
  CDropdown,
  CImage

} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import { cilLockLocked, cilUser } from '@coreui/icons'
import { BrowserRouter as Router, Routes, useNavigate, useLocation, Route, Link } from "react-router-dom";
import { useSelector, useDispatch } from 'react-redux'
import { decrement, login } from '../../auth/reducers/index'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import LocalStorage from '../../util/localStorage';
import '../../login.css'
import axios from 'axios';
import CallApi from '../../util/APICall'
// import ServiceURL from '../configs/ServiceURL';
import ServiceURL from '../../config/ServiceURL';
// import { useNavigate } from "react-router-dom";
import dashboard from 'src/tneb/dashboard';
import 'react-notifications/lib/notifications.css';
import { NotificationContainer, NotificationManager } from 'react-notifications';
import logo from '../../assets/images/twad_logo.png';
const Login = () => {


  const [userName, setUserName] = useState("");
  const [password, setPassword] = useState("");
  const { state } = useLocation();
  const dispatch = useDispatch()
  let navigate = useNavigate()
  const [oldPassword, setOldPassword] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [confirmNewPassword, setConfirmNewPassword] = useState('');
  const [error, setError] = useState('');
  const [showOldPassword, setShowOldPassword] = useState(false);
  const [showNewPassword, setShowNewPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);
  const [passwordChange, setPasswordChange] = useState(false);
  const [loginClick, setLoginClick] = useState(true);
  const userDetails = useSelector((state) => state.twad)
  // let userData = state;

  const chk_Login = (e, data) => {
    const userData = {
      userName: userName,
      userPassword: password
    }
    let url = ServiceURL.LoginDetails
    let ip = userData
    let p = CallApi.callEndPoint(url, ip,)
    p.then((result) => {
      // console.log(result.user.isPasswordChanged, "isPasswordChanged")
      const successMessage = result.message
      
      NotificationManager.success(successMessage, 'success')
      if (result.user.isPasswordChanged === 0) {
        console.log("hh");
        // navigate('/dashboard ', {})
        // console.log(passwordChange,"paaaa");
        setPasswordChange(true)
        setLoginClick(false)
        NotificationManager.info("Change Password ",)
      }
      else {

        navigate('/pension', {})

      }


      dispatch(login({
        userName: result.user.userName, roleId: result.user.roleId, userPassword: result.user.userPassword, employeeName: result.user.employeeName, regionId: result.user.office.regionId, regionName: result.user.office.regionName, circleId: result.user.office.circleId, circleName: result.user.office.circleName, officeId: result.user.office.officeId, officeName: result.user.office.officeName, officeLevelId: result.user.office.officeLevelId, isPassword: result.user.isPasswordChanged
      },))
    }).catch((error) => {
      console.log(error, "error");
      const errorMessage = error.response.data.message
      console.log(errorMessage, "errorMessage");
      NotificationManager.error(errorMessage, 'error',)
    });

  }

  const handleChangeOldPassword = (e) => {
    setOldPassword(e.target.value);
    setError('');
  };

  const handleToggleShowOldPassword = () => {
    setShowOldPassword(!showOldPassword);
  };
  const handleChangeNewPassword = (e) => {
    setNewPassword(e.target.value);
    setError('');
  };

  const handleChangeConfirmNewPassword = (e) => {
    setConfirmNewPassword(e.target.value);
    setError('');
  };

  const handleToggleShowNewPassword = () => {
    setShowNewPassword(!showNewPassword);
  };

  const handleToggleShowConfirmPassword = () => {
    setShowConfirmPassword(!showConfirmPassword);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (newPassword !== confirmNewPassword) {
      setError('New passwords does not match');
    } else {
      // Passwords match, submit form or perform other actions
      console.log('Old Password:', oldPassword);
      console.log('New Password:', newPassword);
    }

  }

  useEffect(() => {

    //console.log(state)
  });
  const saveChangePassword = (e) => {
    // Check if newPassword and confirmNewPassword are empty
    if (!newPassword || !confirmNewPassword) {
      setError('Please fill in both New Password and Confirm New Password fields');
      return; // Exit the function early if fields are empty
    }

    // Check if newPassword and confirmNewPassword match
    if (newPassword !== confirmNewPassword) {
      setError('New passwords do not match');
      return; // Exit the function early if passwords don't match
    }

    // Proceed with password change
    let userName = userDetails.userName;
    // let oldPassword = userDetails.userPassword;
    const userData = {
      userName: userName,
      oldPassword: oldPassword,
      userPassword: newPassword
    };

    let url = ServiceURL.chagePasswordDetails;
    let ip = userData;
    let p = CallApi.callEndPoint(url, ip);

    p.then((result) => {
      if (result.message) {
        setPasswordChange(false);
        setLoginClick(true);
        NotificationManager.success(result.message, 'success');
      }
      // Handle success message
      // const successMessage = result.message;
      // NotificationManager.success(successMessage, 'success');
    }).catch((error) => {
      console.log(error.response.data.message, "erroree");
      const errorMessage = error.response.data.message;
      console.log(errorMessage, "errorMessage");
      NotificationManager.error(errorMessage, 'error');
    });
    setOldPassword("");
    setNewPassword("");
    setConfirmNewPassword("")
  };

  return (
    <div>

      {/* <CHeader> */}

      <div className='row bg-white'>
        <div className='col-md-2 py-3'>
          <CImage
            className="rounded mx-5"
            src={logo}
            alt="slide 3"
            width={145}
            height={145}
          />
        </div>
        <div className='col-md-8 align-self-center '>
          <h1 className='text-center textColor'>Tamil Nadu Water Supply and Drainage Board (TWAD)</h1>
          <h2 className='text-center text-warning'>TWAD-Pension/FAMILY PENSION Details</h2>
        </div>
      </div>
      {/* </CHeader> */}
      <div className="bgcolor" id='bg-color' >
        {(loginClick &&
          <CContainer >
            <CRow className="justify-content-center mt-5">
              <CCol md={6}>
                <CCardGroup>
                  <CCard className="p-4">
                    <CCardBody>
                      <CForm>
                        <h2>{state} Login</h2>
                        <p className="text-medium-emphasis">Sign In to your account</p>
                        <CInputGroup className="mb-3">
                          <CInputGroupText>
                            <CIcon icon={cilUser} />
                          </CInputGroupText>
                          <CFormInput placeholder="Username" autoComplete="username" value={userName} onChange={(e) => setUserName(e.target.value)} />
                        </CInputGroup>
                        <CInputGroup className="mb-4">
                          <CInputGroupText>
                            <CIcon icon={cilLockLocked} />
                          </CInputGroupText>
                          <CFormInput
                            type="password"
                            placeholder="Password"
                            autoComplete="current-password"
                            value={password} onChange={(e) => setPassword(e.target.value)}
                          />
                        </CInputGroup>
                        <CRow>
                          <CCol xs={6}>
                            <CButton className="px-4 login_btn" onClick={(e) => { chk_Login(e, 'chris') }}>
                              Login
                            </CButton>
                          </CCol>
                          <CCol xs={6} className="text-right">
                            <CButton color="" className="px-0 d-none">
                              Forgot password?
                            </CButton>
                          </CCol>
                        </CRow>
                      </CForm>
                    </CCardBody>
                  </CCard>

                </CCardGroup>
              </CCol>
            </CRow>

          </CContainer>
        )}
        {/* <div> */}
        {passwordChange && (
          <CContainer>
            <CRow className="justify-content-center mt-5">
              <CCol md={6}>
                <CCardGroup>
                  <CCard className="p-4">
                    <CCardBody>
                      <CForm onSubmit={handleSubmit}>
                        <h2>{state} Confirm password</h2>
                        <div className="mb-3">
                          <label htmlFor="oldPassword" className="form-label">Old Password:</label>
                          <div className="input-group">
                            <input
                              type={showOldPassword ? "text" : "password"}
                              className="form-control"
                              id="oldPassword"
                              value={oldPassword}
                              onChange={handleChangeOldPassword}
                            />
                            <button
                              className="btn btn-outline-secondary"
                              type="button"
                              onClick={handleToggleShowOldPassword}
                            >
                              {showOldPassword ? <FaEyeSlash /> : <FaEye />}
                            </button>
                          </div>
                          {/* <p>{userDetails.userPassword}</p> */}
                        </div>
                        <div className="mb-3">
                          <label htmlFor="newPassword" className="form-label">New Password:</label>
                          <div className="input-group">
                            <input
                              type={showNewPassword ? "text" : "password"}
                              className="form-control"
                              id="newPassword"
                              value={newPassword}
                              onChange={handleChangeNewPassword}
                            />
                            <button
                              className="btn btn-outline-secondary"
                              type="button"
                              onClick={handleToggleShowNewPassword}
                            >
                              {showNewPassword ? <FaEyeSlash /> : <FaEye />}
                            </button>
                          </div>
                        </div>
                        <div className="mb-3">
                          <label htmlFor="confirmNewPassword" className="form-label">Confirm New Password:</label>
                          <div className="input-group">
                            <input
                              type={showConfirmPassword ? "text" : "password"}
                              className="form-control"
                              id="confirmNewPassword"
                              value={confirmNewPassword}
                              onChange={handleChangeConfirmNewPassword}
                            />
                            <button
                              className="btn btn-outline-secondary"
                              type="button"
                              onClick={handleToggleShowConfirmPassword}
                            >
                              {showConfirmPassword ? <FaEyeSlash /> : <FaEye />}
                            </button>
                          </div>
                          {error && <div className="invalid-feedback d-block">{error}</div>}
                        </div>
                        <div className="mb-3">
                          <button type="submit" className="btn btn-primary col-12" onClick={saveChangePassword}>Change Password</button>
                        </div>
                      </CForm>
                    </CCardBody>
                  </CCard>
                </CCardGroup>
              </CCol>
            </CRow>
          </CContainer>
        )}
        <NotificationContainer />


      </div>
    </div>
  )
}

export default Login
