import React, { useState, useEffect } from 'react';
import { useSelector, } from 'react-redux'
import {
  CCol,
  CWidgetStatsF,
  CCard,
  CCardBody,
  CCardTitle,
  CCardText,
  CImage,
  CRow,
  CSpinner
} from '@coreui/react';

import CIcon from '@coreui/icons-react'
import { cilChartPie } from '@coreui/icons';
import ServiceURL from '../config/ServiceURL';
import '../login.css'
import icon1 from '../assets/images/icons8-dashboard-48 (1).png'
export default function Dashboard() {
  const [metre, setMetre] = useState({});
  const data = useSelector((state) => state.twad)
  const [showMessage, setShowMessage] = useState(false);
  const [loading, setLoading] = useState(true);


  useEffect(() => {

    const timeout = setTimeout(() => {
      setLoading(false);
  }, 2000);

    console.log("refress");
    const officeId = data.office.officeId || 5000;
    const officeLevelId = data.office.officeLevelId
    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ officeId: officeId ,officeLevelId:officeLevelId})
    };


    fetch(ServiceURL.meterDetails + "/status", requestOptions)
      .then(response => response.json())
      .then(data => setMetre(data))
      .catch(error => console.error('Error fetching data:', error));
  return () => clearTimeout(timeout);
  }, []);




  return (
    <div className="container mt-4">
    {loading ? (
      <div className="d-flex justify-content-center align-items-center" style={{ height: '100vh' }}>
        <CSpinner color="primary" variant="grow" />
      </div>
    ) : (
      <>
        <h3 className='mt-4'>LT Connection</h3>
        <CRow className="neumorphic-row">
          <CCol xs={12} sm={6} md={4}>
            <CWidgetStatsF
              className="mb-3 text-white neumorphic-card"
              color="info"
              icon={<CIcon icon={cilChartPie} height={24} />}
              title= "No of LT Connections" 
              // value={metre.filter(item => item.type_of_connection === "LT"||(item.type_of_connection === "LT"&&item.circle_id)).map(item => item.total)}

              value={metre.reduce((tot, item) => {
                if (item.type_of_connection === "LT"||(item.type_of_connection === "LT"&&item.circle_id)) {
                  tot += item.total;
                }
                return tot;
              }, 0)}
              
              
            />
          </CCol>
          <CCol xs={12} sm={6} md={4}>
            <CWidgetStatsF
              className="mb-3 text-white neumorphic-card"
              color="success"
              icon={<CIcon icon={cilChartPie} height={24} />}
              title="Mapped"
              value={metre.reduce((tot, item) => {
                if (item.type_of_connection === "LT"||(item.type_of_connection === "LT"&&item.circle_id)) {
                  tot += item.mapped;
                }
                return tot;
              }, 0)}
            />
          </CCol>
          <CCol xs={12} sm={6} md={4}>
            <CWidgetStatsF
              className="mb-3 text-white neumorphic-card"
              color="danger"
              icon={<CIcon icon={cilChartPie} height={24} />}
              title="Not Mapped"
              value={metre.reduce((tot, item) => {
                if (item.type_of_connection === "LT"||(item.type_of_connection === "LT"&&item.circle_id)) {
                  tot += item.not_mapped;
                }
                return tot;
              }, 0)}
            />
          </CCol>
        </CRow>
  
        <h3 className='mt-4'>HT Connection</h3>
        <CRow className="neumorphic-row">
          <CCol xs={12} sm={6} md={4}>
            <CWidgetStatsF
              className="mb-3 text-white neumorphic-card"
              color="info"
              icon={<CIcon icon={cilChartPie} height={24} />}
              title="No of HT Connections"
              value={metre.reduce((tot, item) => {
                if (item.type_of_connection === "HT"||(item.type_of_connection === "HT"&&item.circle_id)) {
                  tot += item.total;
                }
                return tot;
              }, 0)}
            />
          </CCol>
          <CCol xs={12} sm={6} md={4}>
            <CWidgetStatsF
              className="mb-3 text-white neumorphic-card"
              color="success"
              icon={<CIcon icon={cilChartPie} height={24} />}
              title="Mapped"
              value={metre.reduce((tot, item) => {
                if (item.type_of_connection === "HT"||(item.type_of_connection === "HT"&&item.circle_id)) {
                  tot += item.mapped;
                }
                return tot;
              }, 0)}
            />
          </CCol>
          <CCol xs={12} sm={6} md={4}>
            <CWidgetStatsF
              className="mb-3 text-white neumorphic-card"
              color="danger"
              icon={<CIcon icon={cilChartPie} height={24} />}
              title="Not Mapped"
              value={metre.reduce((tot, item) => {
                if (item.type_of_connection === "HT"||(item.type_of_connection === "HT"&&item.circle_id)) {
                  tot += item.not_mapped;
                }
                return tot;
              }, 0)}
            />
          </CCol>
        </CRow>
      </>
    )}
  </div>
  
  );
}
